.loader {
  width: 48px;
  height: 48px;
  display: inline-block;
  position: relative;
  --radius: 6px
}
.loader::after,
.loader::before {
  content: '';
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: var(--radius) solid #fff;
  position: absolute;
  left: 0;
  top: 0;
  animation: animloader 2s linear infinite;
}
.loader::after {
  animation-delay: 1s;
}
.loaderLight {
  composes: loader;
}
.loaderLight::after,
.loaderLight::before {
  border: var(--radius) solid #000000;
}
@keyframes animloader {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
